import { ComponentType, ReactNode } from 'react';

export type ConfirmationProps = {
    /**
     *  
     */
    screen: ConfirmationScreen | string;

    /**
     *  
     */
    state: ConfirmationState | string;

    /**
     *     
     */
    mobile?: boolean;

    /**
     *  
     */
    alignContent?: 'left' | 'center';

    /**
     *   
     */
    texts?: ConfirmationTexts;

    /**
     *  ,         ,   
     */
    requiredCharAmount?: number;

    /**
     *        ,  
     */
    countdownDuration?: number;

    /**
     *    (ms)
     */
    tempBlockDuration?: number;

    /**
     *  ,    
     */
    phone?: string;

    /**
     *     
     */
    blockSmsRetry?: boolean;

    /**
     *     
     */
    dataTestId?: string;

    /**
     * ss     
     */
    className?: string;

    /**
     *  -       
     */
    clearCodeOnError?: boolean;

    /**
     *    
     */
    onChangeState: (state: ConfirmationState | string) => void;

    /**
     *    
     */
    onChangeScreen: (state: ConfirmationScreen | string) => void;

    /**
     *      
     */
    onInputFinished: (code: string) => void;

    /**
     *      " "
     */
    onSmsRetryClick: () => void;

    /**
     *    ""    
     */
    onFatalErrorOkButtonClick?: () => void;

    /**
     *    
     */
    onTempBlockFinished?: () => void;

    /**
     *  ,   -   (screen),  -   
     */
    getScreensMap?: (defaulScreensMap: ScreensMap) => ScreensMap;

    /**
     *  .
     */
    children?: ReactNode;

    /**
     *      
     * @default false
     */
    hideCountdownSection?: boolean;
};

export type TConfirmationContext = Required<
    Pick<
        ConfirmationProps,
        | 'alignContent'
        | 'texts'
        | 'state'
        | 'screen'
        | 'requiredCharAmount'
        | 'onInputFinished'
        | 'countdownDuration'
        | 'onChangeState'
        | 'onSmsRetryClick'
        | 'onChangeScreen'
        | 'onFatalErrorOkButtonClick'
        | 'tempBlockDuration'
        | 'hideCountdownSection'
    >
> &
    Pick<
        ConfirmationProps,
        'phone' | 'blockSmsRetry' | 'onTempBlockFinished' | 'clearCodeOnError'
    > & {
        timeLeft: number;
    };

export type ConfirmationTexts = {
    /**
     *  INITIAL
     */
    title?: ReactNode; // 
    codeError?: string; //   
    codeChecking?: string; //  
    codeSending?: string; //  
    codeSended?: string; //  
    buttonRetry?: string; //    
    linkToHint?: string; //    HINT
    noAttemptsLeft?: string; //     
    countdown?: string; // '   '

    /**
     *  HINT
     */
    hintButton?: string; //  ''

    /**
     *  FATAL_ERROR
     */
    fatalErrorTitle?: ReactNode; // 
    fatalErrorDescription?: ReactNode; // 
    fatalErrorButton?: string; // 

    /**
     *  TEMP_BLOCK
     */
    tempBlockTitle?: ReactNode; // 
    tempBlockDescription?: ReactNode; // 
};

export type ConfirmationScreen =
    | 'INITIAL' //  
    | 'HINT' //  "  ?"
    | 'FATAL_ERROR' //   
    | 'TEMP_BLOCK'; //   

export type ConfirmationState =
    | 'INITIAL' //  
    | 'CODE_CHECKING' //  
    | 'CODE_SENDING' //   
    | 'CODE_ERROR'; // ,    

export type ScreensMap = {
    [key: string]: ComponentType;
};

