import React, { useMemo } from 'react';
import cn from 'classnames';

import { ResponsivePropertyType } from '../typings';
import { createClassNames } from '../utils';

import guttersStyles from '../gutters.module.css';
import styles from './index.module.css';

export type RowProps = {
    /**
     *  
     */
    className?: string;

    /**
     *    .
     *  : `[0, 8, 16, 24]`
     *  `{ mobile: [0..24], tablet: [0..24], desktop: [0..24] }`
     *  `{ mobile: { xs: [0..24], s: [0..24], m: [0..24], l: [0..24] },
     * tablet: { s: [0..24], m: [0..24] },
     * desktop: { s: [0..24], m: [0..24], l: [0..24], xl: [0..24] } }`.
     */
    gutter?: ResponsivePropertyType;

    /**
     *      
     */
    align?: 'top' | 'middle' | 'bottom';

    /**
     *      
     */
    justify?: 'left' | 'center' | 'right' | 'around' | 'between';

    /**
     * Html  .
     * - <a href="https://github.com/philipwalton/flexbugs#flexbug-9" target="_blank">  </a>,
     *   ,  
     *   HTML  flex-.
     */
    tag?: keyof JSX.IntrinsicElements;

    /**
     * 
     */
    children?: React.ReactNode;

    /**
     *     
     */
    dataTestId?: string;
};

export const Row: React.FC<RowProps> = ({
    tag: Component = 'div',
    className,
    gutter = {
        mobile: {
            s: 16,
        },
        desktop: {
            m: 24,
        },
    },
    align,
    justify = 'between',
    children,
    dataTestId,
}) => {
    const classNames = useMemo(() => createClassNames({ gutter }, guttersStyles), [gutter]);

    return (
        <Component
            className={cn(
                guttersStyles.row,
                styles.component,
                align && styles[align],
                styles[justify],
                ...classNames,
                className,
            )}
            data-test-id={dataTestId}
        >
            {children}
        </Component>
    );
};
